.rocket-div ul {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  padding: 10px 25px;
}

.rocket-div ul li {
  display: flex;
  flex-direction: row;
  height: 190px;
  padding: 5px;
}

.img-rocket {
  padding: 10px 10px 10px 25px;
}

.img-rocket img {
  height: 170px;
  width: 190px;
  border-radius: 10px;
}

.rocket-detail {
  display: flex;
  flex-direction: column;
  padding: 10px;
  text-align: left;
}

.rocket-detail h2 {
  padding-top: 0;
  padding-bottom: 10px;
  margin: 0;
  font-size: 22px;
  font-weight: 600;
}

.rocket-detail p {
  font-size: 17px;
  padding-bottom: 5px;
  line-height: 20px;
  padding-right: 20px;
  font-weight: 400;
}

.rocket-detail span {
  padding: 4px;
  font-size: 14px;
  font-weight: normal;
  margin-right: 5px;
}

.rocket-detail .cancelBtn {
  width: 200px;
  border-radius: 5px;
  padding: 10px 5px;
  font-size: 17px;
  font-weight: 400;
  background-color: transparent;
  border: 1px solid black;
  box-shadow: 5px 6px rgb(231, 230, 230);
}

.rocket-detail .reserveBtn {
  width: 150px;
  border-radius: 5px;
  padding: 10px 5px;
  font-size: 17px;
  font-weight: 400;
  border: none;
  background-color: #0290ff;
  color: #fff;
  letter-spacing: 0.5px;
}
